import { Component, OnInit } from '@angular/core';
import { IconsService } from '../icons.service';
import { FormControl } from '@angular/forms';
import { ColorpickerComponent } from "../colorpicker/colorpicker.component";

import {Observable}  from 'rxjs/Observable';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/throttleTime';
import 'rxjs/add/observable/fromEvent';

@Component({
  selector: 'app-importer',
  templateUrl: './importer.component.html',
  styleUrls: ['./importer.component.less']
})

export class ImporterComponent implements OnInit {
  icons = [];
  baseUrl = '';
  groups = [];
  searchResults = [];
  paginationSize = 100;
  lastIndex = 50;
  query = new FormControl();
  container: any;
  iconsToShow: any;
  chosenColor: any = {
    name: 'grey600',
    value: '#757575'
  };

  constructor(private iconsService: IconsService) {
    this.query.valueChanges
      .debounceTime(100)
      .subscribe(term => {
        this.updateSearch(term);
      })
   }

  onScroll() {
    let nextPage = this.searchResults.slice(this.iconsToShow.length, this.iconsToShow.length + this.paginationSize);
    if (nextPage.length > 0) {
      this.iconsToShow = this.iconsToShow.concat(nextPage);
    }
  }

  ngOnInit() {
    this.container = document.getElementById('search-results');
    this.iconsService.getIcons()
      .subscribe(
        (data) => {
          this.icons = data['icons'];
          this.baseUrl = data['base_path'];
          this.groups = data['groups'];
          this.searchResults = this.icons;
          this.iconsToShow = this.icons.slice(0, this.paginationSize);
          // this.iconsToShow = this.icons;
        },
        (error) => console.log("error : " + error)
      );
  }

  groupName = function (key) {
    let group = this.groups.find(item => {
      if (item.data.id == key) {
        return true;
      }
    })
    return group.data.name;
  }

  updateSearch = function (query) {
    // if (!query) {
    //   this.searchResults = this.icons;
    // }
    this.searchResults = this.icons.filter(icon => {
      let nameSearch = icon.name.toLowerCase().indexOf(query.toLowerCase()) > -1;
      let keywords = icon.keywords.join(' ');
      let isKeyword = true;
      let queryWords = query.split(' ');

      for (var i = 0; i < queryWords.length; i++) {
        if (keywords.indexOf(queryWords[i]) == -1) {
          isKeyword = false;
        }
      }
      return nameSearch || isKeyword;
    });

    this.iconsToShow = this.searchResults.slice(0, this.paginationSize);
    // this.iconsToShow = this.searchResults;
  }

  onColorPicked(color) {
    this.chosenColor = color;
  }

  isGlif(group_id) {
    let groups: any = ['avatars_and_widgets', 'alphabet_logos', 'internal_logos', 'non_material_product_logos', 'product_logos'];
    return !groups.includes(group_id)
  }

  dragEnd(icon) {
    let isGlif = this.isGlif(icon.group_id);
    let colorExtension = isGlif ? "_" + this.chosenColor.name : '';
    let data = {
      name: icon.id + colorExtension,
      colorName: this.chosenColor.name,
      colorValue: this.chosenColor.value,
      isGlif: isGlif
    }
    window['ga']('send', 'event', 'icons panel', 'dragged', 'icons');
    if (window['MDAction']) {
      window['MDAction']('drag-end', data);
    }
  }

}



